window.onGetPlatformMsg = (jsonString) =>
{
    //
    console.log("receive message from JAVA:" + jsonString)
    var jsonObj = jsonString
    if (typeof(jsonString) == "string") 
    {
        jsonObj = JSON.parse(jsonString);
    }
    if(window.onHandlerPlatformMsg != null)
    {
        window.onHandlerPlatformMsg(jsonObj);//onHandlerPlatformMsgSdkHandler.ts
    }else
    {
        console.log("onHandlerPlatformMsg")
    }

}
