class AppBuriedDataBankruptcyVo {
    bankruptcyCount: number = 0//
    bankruptcyGiveGold: number = 0//
    buriedDataEnum: number = 0//
    goldId: number = 0
    matchId: number = 0
    constructor() { }
}

class AppBuriedDataOrderVo {
    createCount: number = 0//
    orderDataType: number = 0//
    orderSourceType: number = 0//0 1 2
    payOutOrderId: string = ""//
    showCount: number = 0//
    constructor() { }
}

class AppBuriedDataShareVo {
    backCount: number = 0//
    buriedDataEnum: number = 0//
    shareCount: number = 0//
    goldId: number = 0
    matchId: number = 0
    shareSmallId: number = 0
    channelId: number = 0//
    constructor() { }
}

export default class MakePointsManager {

    static AppBuriedDataComplexVo = { "bankruptcyList": [], "orderList": [], "shareList": [] }
    static maxLength = 30
    static isUploading = false
    static tempData = { "bankruptcyList": [], "orderList": [], "shareList": [] }

    static getUrl() {
        let requestData: IUrlRequestData = {
            baseUrl: AppData.httpCurApiUrl + "data/buried?",
            params: {
                uid: ModelData.instance.roleData.id,
                gameId: AppData.apiGameName,
            }
        }
       // GameUtil.addPublicParams(requestData)
        let t = GameUtil.get_tokenAndTimestamp(requestData.params["uid"])
        if (t) {
            requestData.params["token"] = t[0]
            requestData.params["timestamp"] = t[1]
        }
        return GameUtil.formatParamsForHttp(requestData)
    }

    //
    static shareRecord(type: MakePointType, id: number = 0, backCount = 0, shareSmallId = 0, channelId = 0) {
        let data = {}
        data["buriedDataEnum"] = type
        if (backCount > 0) {
            data["backCount"] = backCount
        } else {
            data["shareCount"] = 1
        }
        if (type == MakePointType.SHARE_MATCH_LOSE || type == MakePointType.SHARE_SIGN_MATCH || type == MakePointType.SHARE_MATCH_WIN) {
            data["matchId"] = id
        } else if (type == MakePointType.SHARE_GOLD_WIN || type == MakePointType.SHARE_GOLD_LOSE || type == MakePointType.SHARE_RANKING_MATCH_WIN
            || type == MakePointType.SHARE_RANKING_MATCH_LOSE || type == MakePointType.SHARE_GOLD_SHOW_CARDS) {
            data["goldId"] = id
        }
        if (shareSmallId) {
            data["shareSmallId"] = shareSmallId
        }
        if (channelId) {
            data["channelId"] = channelId
        }
        if (this.AppBuriedDataComplexVo.shareList.length >= this.maxLength) {
            Debug.warn("")
            this.tempData.shareList.push(data)
            this.uploadAll()
            return
        }

        if (this.isUploading) {
            this.tempData.shareList.push(data)
        } else {
            if (this.tempData.shareList.length > 0) {
                let length = this.tempData.shareList.length
                for (let i = 0; i < length; i++) {
                    this.AppBuriedDataComplexVo.shareList.push(this.tempData.shareList[i])
                }
                this.tempData.shareList = []
            }
            this.AppBuriedDataComplexVo.shareList.push(data)
        }
        Debug.warn("", this.AppBuriedDataComplexVo.shareList)
        this.checkLength()
    }

    //
    static bankrupRecord(type: MakePointType, count: number, id: number = 0, bankruptcyGiveGold: number = 0) {
        let data = {}
        data["buriedDataEnum"] = type
        if (count) {
            data["bankruptcyCount"] = count
        }
        if (bankruptcyGiveGold) {
            data["bankruptcyGiveGold"] = bankruptcyGiveGold
        }
        if (type == MakePointType.BANKRUP_MATCH) {
            data["matchId"] = id
        } else if (type == MakePointType.BANKRUP_GOLD || type == MakePointType.BANKRUP_RANKING_MATCH_LOSE || type == MakePointType.BANKRUP_RANKING_MATCH_WIN) {
            data["goldId"] = id
        }

        if (this.AppBuriedDataComplexVo.bankruptcyList.length >= this.maxLength) {
            Debug.warn("")
            this.tempData.bankruptcyList.push(data)
            this.uploadAll()
            return
        }

        if (this.isUploading) {
            this.tempData.bankruptcyList.push(data)
        } else {
            if (this.tempData.bankruptcyList.length > 0) {
                let length = this.tempData.bankruptcyList.length
                for (let i = 0; i < length; i++) {
                    this.AppBuriedDataComplexVo.bankruptcyList.push(this.tempData.bankruptcyList[i])
                }
                this.tempData.bankruptcyList = []
            }
            this.AppBuriedDataComplexVo.bankruptcyList.push(data)
        }
        Debug.warn("", this.AppBuriedDataComplexVo.bankruptcyList)
        this.checkLength()
    }

    //
    static orderRecord(type: MakePointType, showCount = 0, orderSourceType = 0, createCount = 0, payOutOrderId: string = "") {
        let data = {}
        if (createCount) {
            data["createCount"] = createCount
        }
        if (showCount) {
            data["showCount"] = showCount
        }
        data["orderSourceType"] = orderSourceType
        if (payOutOrderId != "") {
            data["payOutOrderId"] = payOutOrderId
        }
        data["orderDataType"] = type

        if (this.AppBuriedDataComplexVo.orderList.length >= this.maxLength) {
            Debug.warn("")
            this.tempData.orderList.push(data)
            this.uploadAll()
            return
        }

        if (this.isUploading) {
            this.tempData.orderList.push(data)
        } else {
            if (this.tempData.orderList.length > 0) {
                let length = this.tempData.orderList.length
                for (let i = 0; i < length; i++) {
                    this.AppBuriedDataComplexVo.orderList.push(this.tempData.orderList[i])
                }
                this.tempData.orderList = []
            }
            this.AppBuriedDataComplexVo.orderList.push(data)
        }
        Debug.warn("", this.AppBuriedDataComplexVo.orderList)
        this.checkLength()
    }

    static checkLength() {
        let length = this.AppBuriedDataComplexVo.shareList.length + this.AppBuriedDataComplexVo.bankruptcyList.length + this.AppBuriedDataComplexVo.orderList.length
        if (length >= this.maxLength) {
            this.uploadAll()
        }
    }


    //
    static uploadAll() {
        if (!ModelData.instance.roleData.id) {
            return
        }
        if (this.isUploading) {
            return
        }
        if (this.AppBuriedDataComplexVo.bankruptcyList.length == 0 && this.AppBuriedDataComplexVo.shareList.length == 0 && this.AppBuriedDataComplexVo.orderList.length == 0) {
            return
        }
        this.isUploading = true
        let self = this
        let url = this.getUrl()
        let data = JSON.stringify(this.AppBuriedDataComplexVo)
        self.http(url, data, (res) => {
            self.isUploading = false
            self.AppBuriedDataComplexVo.bankruptcyList = []
            self.AppBuriedDataComplexVo.orderList = []
            self.AppBuriedDataComplexVo.shareList = []
            Debug.warn("")
        }, () => {
            self.isUploading = false
            Debug.warn("")
        })
    }


    static http(url, data: any, success: Function, fail: Function) {
        Debug.warn(url, data)
        var xhr = new XMLHttpRequest();
        xhr.open("POST", url, true);//OPENsetRequestHeader  
        xhr.setRequestHeader("Content-Type", "application/json");
        xhr.onreadystatechange = function () {
            if (xhr.readyState == 4) {
                if (xhr.status == 200) {
                    if (xhr.responseText && xhr.responseText != "") {
                        var response = JSON.parse(xhr.responseText);
                        Debug.log("", response)
                        if (!response) {
                            fail()
                            Debug.error("http", xhr.responseURL, xhr.responseText)
                            return
                        }
                        success(response)
                    } else {
                        success()
                    }
                } else {
                    fail()
                    Debug.log("http request error:", xhr.status)
                }
            }
        };
        xhr.send(data);
    }


}