
import { Card } from "./poker_data";
import { PokerRecommend } from "./poker_recommend";
import { RecommendTools } from "./recommend_tools";

const { ccclass, property } = cc._decorator;

export enum PatternType{
    type_unknow = 0,        //
    type_danpai = 1,        //K
    type_duizi = 2,         //2K+K
    type_sandaiyi = 3,      //3+1KKK+8QQQ+44
    type_feiji = 4,         //21+1333444+78333444+7788
    type_shunzi = 5,        //578910J3456789
    type_liandui = 6,       //3334455991010JJQQ
    type_zhadan = 7,        //4KKKKQQQQ3333
    type_huojian = 8,       //
    type_sidaier = 9,       //4+2KKKK+898888+AA
}

export class CardPattern{
    cards: number[] = []
    value: number = 0
    type: PatternType = PatternType.type_unknow
    repeated_cnt: number = 1
    card_cnt: number = 0
    disp_type: string    
    feijinum: number = 0
    
    static newPattern(codeList){        
        let pattern = new CardPattern()
        if(pattern.parse(codeList)){
            return pattern
        }
        return null
    }


    compable(pattern: CardPattern): boolean{
        if(this.type <= PatternType.type_unknow){
            console.error(new Error('this.type='+this.type+"<=PatternType.type_unknow").stack)
        }
        if(pattern.type <= PatternType.type_unknow){
            console.error(new Error('pattern.type='+pattern.type+"<=PatternType.type_unknow").stack)
        }
        if(this.type == PatternType.type_zhadan || this.type == PatternType.type_huojian){
            return true
        }else{
            return this.card_cnt == pattern.card_cnt && this.type == pattern.type
        }
    }

    le(pattern: CardPattern){
        //,
        let cardTypeFactor = [0, 0, 0, 0, 0, 0, 0, 100, 200, 0 ]
        if(pattern == null){
            console.error(new Error('pattern=null').stack)
        }
        if(!this.compable(pattern)){
            console.error(new Error('').stack)
        }
        let maxValue = RecommendTools.Max(Card.cardValueMap)
        if(this.value > maxValue){
            console.error(new Error('this.value > maxValue,' + this.value).stack)
        }
        if(pattern.value > maxValue){
            console.error(new Error('pattern.value > maxValue,' + pattern.value).stack)
        }
        if((this.value + cardTypeFactor[this.type]) <= (pattern.value + cardTypeFactor[pattern.type])){
            return true
        }else{
            return false
        }
    }


    parseDispType(){
        let dispTypeOfSingle = [ "one1", "one2", "one3", "one4", "one5", "one6", "one7", "one8", "one9", "one10", "one11", "one12", "one13", "one14", "one15" ]
        let dispTypeOfDouble = [ "dui1", "dui2", "dui3", "dui4", "dui5", "dui6", "dui7", "dui8", "dui9", "dui10", "dui11", "dui12", "dui13" ]
        if(this.type == PatternType.type_danpai){
            this.disp_type = dispTypeOfSingle[PokerRecommend.value2cardvalue(this.value)]
        }else if(this.type == PatternType.type_duizi){
            this.disp_type = dispTypeOfDouble[PokerRecommend.value2cardvalue(this.value)]
        }else if(this.type == PatternType.type_sandaiyi){
            if(this.card_cnt == 3){
                this.disp_type = 'sanzhang'
            }else if(this.card_cnt == 4){
                this.disp_type = 'sandaiyi'
            }else if(this.card_cnt == 5){
                this.disp_type = 'sandaier'
            }
        }else if(this.type == PatternType.type_shunzi){
            this.disp_type = 'shunzi'
        }else if(this.type == PatternType.type_liandui){
            this.disp_type = 'liandui'
        }else if(this.type == PatternType.type_zhadan){
            this.disp_type = 'zhadan'
        }else if(this.type == PatternType.type_feiji){
            this.disp_type = 'feiji'
        }else if(this.type == PatternType.type_huojian){
            this.disp_type = 'huojian'
        }else if(this.type == PatternType.type_sidaier){
            if(this.card_cnt == 6){
                this.disp_type = 'sidaier'
            }else{
                this.disp_type = 'sidaisi'
            }
        }
    }

    //true,
    parse(codeList: number[]){
        if(codeList != null){
            this.cards = []
            for (let i = 0; i < codeList.length; i++) {
                const code = codeList[i];
                this.cards.push(code)
            }
        }
        this.card_cnt = this.cards.length
        let card_type_vale = PokerRecommend.get_cards_type(codeList)
        if(card_type_vale.card_type == PatternType.type_unknow){
            return false
        }else{
            this.type = card_type_vale.card_type
            this.value = card_type_vale.card_value     
            this.feijinum = card_type_vale.feijinum       
            this.parseDispType()
            return true
        }
    }

}
