
export module Club{

    //
    export interface IClubData{
        clubId:number;//id
        leaderId:number;
        clubName:string;
        leaderName:string;
        leaderUrl:string;
        announcement:string;
        isSelect:boolean;//
    }

    export enum TableType{
        PWTable = 0,//
        GameTable = 1,//
    }

    //


    export interface IPlayer{
        id:number,
        name:string,
        avatarUrl:string;
    }

    export interface IPlayWayInfo{
        id:string,
        clubId:number,
        gameId:number,
        playwayName:string,
        payMode:number,
        payCount:number,
        isPercentage:number,
        sorceCell:number,
        lowerLimit:number,
        upperLimit:number,
        gameParameter:any
    }

    //
    export interface IGameTableInfo{
        host: string,
		port: number,
		clubId: number,
		gameId: number,
		tableId: number,
		gameCount: number,
		currentCount: number,
		chairCount: number,
		playwayId: string,
		playwayName: string,
		gameMode: number,
		lowerLimit: number,
		upperLimit: number,
		players: IPlayer[],
    }

    //
    export interface ITableInfo{
        playWay?:IPlayWayInfo;
        gameInfo?:IGameTableInfo;
        tbType:TableType;
        tableid:number|string;
    }

    //
    export function rank(allTables:ITableInfo[]){
        allTables.sort(function(a:Club.ITableInfo,b:Club.ITableInfo){
            return getSortIndex(a) - getSortIndex(b);
        })
    }

    // 
    function getSortIndex(tableData: any) {
        if (tableData.tbType == Club.TableType.GameTable) {
            if (tableData.gameInfo.chairCount > tableData.gameInfo.players.length) {
                return -1;
            } else {
                return 999;
            }
        } else {
            return 0;
        }
    }
}
